#title {
  width: 100%;
  height: 11rem;

  padding: 2rem 0 0;

  font: {
    size: 2.2rem;
    family: "Black Han Sans", sans-serif;
  }

  text-align: center;
  line-height: 15rem;

  @include tablet {
    height: 8rem;

    padding: 8rem 0 0;

    font-size: 3rem;
  }

  @include mobile {
    height: 10rem;

    padding: 20rem 0 0;

    font-size: 3.5rem;
  }
}
